@font-face {
  font-family: "Avenir";
  src: url("./fonts/Avenir-Book.ttf") format("ttf"),
    url("./fonts/Avenir-Heavy.ttf") format("ttf"),
    url("./fonts/Avenir.ttc") format("ttc");
}

* {
  box-sizing: border-box;
}

body {
  font-family: Avenir, sans-serif;
  font-weight: 300;
  margin: 0;
  padding: 0 8px;

}

.search-form {
  margin-bottom: 20px;
  position: relative;
}

.search-form__field {
  font-family: Avenir, sans-serif;
  display: block;
  width: 100%;
  height: 100%;
  background-color: #fff;
  border-radius: 4px;
  border: 1px solid #e1e7ed;
  box-shadow: 0 1px 8px rgba(61, 78, 97, 0.1);
  outline: 0;
}

.search-form__field::placeholder {
  color: #3d4e61;
}

.search-form__field:hover {
  box-shadow: 0 3px 8px rgba(61, 78, 97, 0.2);
}

.search-form__field:focus {
  font-weight: 900;
  background: linear-gradient(180deg, #fff 0%, #f6f6f7 100%);
  border: 1px solid #e1e7ed;
  border-radius: 3px;
  color: #3d4e61;
}

.big-field {
  height: 70px;
  padding-left: 62px;
  font-size: 16px;
  line-height: 22px;
}

[data-qa="big"] {
  margin-top: 20px;
}

[data-qa="big"]::before {
  content: "";
  display: inline-block;
  position: absolute;
  top: 25px;
  left: 26px;
  width: 19px;
  height: 19px;
  background-image: url(./images/Search.svg);
  background-position: center;
  background-size: cover;
}

.small-field {
  height: 42px;
  padding-left: 33px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 19px;
}

[data-qa="small"]::before {
  content: "";
  display: block;
  position: absolute;
  top: 15px;
  left: 13px;
  width: 11px;
  height: 11px;
  background-image: url(./images/Search.svg);
  background-position: center;
  background-size: cover;
}
